<template>
  <v-card class="rounded-lg mx-auto" max-width="360" flat>
    <v-card-title class="font-weight-bold display-1 justify-center text-center">
       ,   
    </v-card-title>
    <v-card-subtitle class="text-center title">
         
    </v-card-subtitle>
    <v-card-text>
      <v-dialog
        ref="menu"
        v-model="menu"
        class="rounded-xl"
        transition="scale-transition"
        max-width="480"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            class="rounded-lg"
            v-model="date"
            label="YYYY-MM-DD"
            solo
            single-line
            readonly
            hide-details
            outlined
            flat
            v-bind="attrs"
            @blur="date = parseDate(dateFormatted)"
            v-on="on"
          />
        </template>
        <v-card class="rounded-xl" outlined>
          <v-date-picker
            full-width
            v-model="date"
            no-title
            :max="$moment().format('YYYY-MM-DD')"
            locale="ko"
            @input="menu = false"
          />
        </v-card>
      </v-dialog>
    </v-card-text>
    <v-card-actions class="px-4">
      <v-btn
        @click="calculateDate"
        class="rounded-lg body-1 font-weight-bold"
        depressed
        :disabled="!date"
        :ripple="false"
        block
        x-large
        color="primary"
      >
        
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    date: "",
    dateFormatted: "",
    menu: false,
  }),

  computed: {
    computedDateFormatted() {
      return this.formatDate(this.date);
    },
  },

  watch: {
    date() {
      this.dateFormatted = this.formatDate(this.date);
    },
  },

  methods: {
    calculateDate() {
      if (!this.date) return;

      this.$router.push();
      this.routerPush("Result", this.date);
    },

    routerPush(routerName, id) {
      if (id === "") {
        return;
      }

      if (this.$router.currentRoute.params.id !== id) {
        this.$router
          .push({ name: routerName, params: { id: id } })
          .then(() => window.scrollTo(0, 0));
      }
    },
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${month}/${day}/${year}`;
    },
    parseDate(date) {
      if (!date) return null;
      const [month, day, year] = date.split("/");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
  },
};
</script>

